  content: '';
  background: #2199e8;
  position: absolute;
  display: block; }

.job::after {
  /* the line */
  height: 100%;
  width: 4px;
  left: -24px;
  top: 7px; }

.job::before {
  /* the dot */
  width: 1rem;
  height: 1rem;
  left: -30px;
  top: 6px;
  border-radius: 50%; }

.job.end {
  padding-bottom: 0; }

.job.end::after {
  background: #fff;
  z-index: -1; }

@media screen and (min-width: 40em) {
